import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import axios from 'axios';

const Detail = (props) => {

    let [alert, setAlert] = useState(true);
    let [inputData, setInputData] = useState('');

    const history = useHistory();
    const { id } = useParams();
    const product = props.shoes.find(item => item.id == id);

    
    /*
    useEffect(( ) => {
        axios.get('https://....json').then(( ) => { }).catch(( ) => { });
    }, []);
    */

    useEffect(() => {
        let timer = setTimeout(() => { setAlert(false) }, 2000)
        return () => { clearTimeout(timer) }
    },[alert]);

    return (
        <div className="container">
            <div className="red">Detail</div>
            { alert === true
                ? ( <div className="my-alert-red">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>)
                : null
            }
            <div className="row">
            <div className="col-md-6">
                <img src={ product.imgUrl || 'https://codingapple1.github.io/shop/shoes' + (product.id + 1) + '.jpg' } width="100%" alt={ product.title + ' Goods Picture' } />
            </div>
            <div className="col-md-6 mt-4">
                <h4 className="pt-5">{ product.title }</h4>
                <p>{ product.content }</p>
                <p>{ product.price }원</p>

                <Info stock={ props.stock } />

                <button className="btn btn-danger" onClick={ () => {
                    let newStock = [...props.stock]
                    newStock[0] = newStock[0] - 1
                    props.setStock(newStock)
                    }}>주문하기</button>
                <button className="btn btn-danger" onClick={ () => { history.push('/') } }>HOME</button>
                <button className="btn btn-danger" onClick={ () => { history.goBack() } }>뒤로가기</button>
            </div>
            </div>
        </div> 
    )
}

function Info(props){
    return (
        <p>재고 : { props.stock[0] }</p>
    )
}




export default Detail;