import React, { useState } from 'react';
import './App.css';
import { Navbar, Nav, NavDropdown, Button, Jumbotron } from 'react-bootstrap';
import ProductsData from './data.js';
import Product from './components/Product.js';
import { Link, Route, Switch } from 'react-router-dom';
import Detail from './components/Detail.js';
import axios from 'axios';

function App() {

  let [shoes, setShoes] = useState(ProductsData);
  let [shoesMoreToggle, setShoesMoreToggle] = useState(false);
  let [stock, setStock] = useState([10,11,12]);
  
  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">Shoes Shop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Switch>
        <Route path="/" exact>
          <Jumbotron className="background">
            <h1>20% Season OFF</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>

          <div className="container">
            <div className="row">
              <Product shoes={ shoes } />
            </div>
            { shoesMoreToggle === false
              ? ( <button className="btn btn-primary" onClick={() => {
                    /* 로딩중이라는  UI 띄움 */
                    /* axois.post('서버url', { id : 'a1234', pw : 1234 }).then(); */
                    axios.get('https://codingapple1.github.io/shop/data2.json')
                      .then(response => {
                        /* 로딩중이라는  UI 안보임 처리 */
                        setShoes([...shoes, ...response.data])
                        setShoesMoreToggle(true)
                      })
                      .catch(() => {
                        /* 로딩중이라는  UI 안보임 처리 */
                      });
                  }}>더보기</button>
              )
              : null
            }

          </div>
        </Route>
        <Route path="/detail/:id">
          <Detail shoes={ shoes } stock={ stock } setStock={setStock} />
        </Route>

        {/*<Route path="/어쩌구" component={ Modal }></Route> // HTML 말고 컴포넌트를 보여줄 때*/}

        <Route path="/:id">
          <div>아무거나 적었을때</div>
        </Route>
      </Switch>

    </div>
  );
}

export default App;
